import {
    auth,
    ref,
    database,
} from './firebase';
import createIframe from './createIframe';

window.sgliveSSO = {
    getPage(id, uid, callback) {
        const websiteRef = ref.child(`websites/${id}`);
        websiteRef
            .once('value')
            .then((snap) => {
                const val = snap.val();
                const edited = (val && val.onEdit && val.onEdit.status);
                if (edited) {
                    val.edited = true;
                } else {
                    if (val && val.displayName) {
                        this.editedId = id;
                        websiteRef
                            .update({
                                onEdit: {
                                    status: true,
                                    user: uid,
                                },
                            });
                        websiteRef
                            .onDisconnect()
                            .update({
                                onEdit: {
                                    status: false,
                                    user: uid,
                                },
                            });
                    }
                }
                callback(val);
            });
    },
    savePage(id, callback) {
        const websitesRef = ref.child(`websites`);
        const uuidv4 = () => {
            return ([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g, (c) => {
                return (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16);
            });
        };
        websitesRef
            .once('value')
            .then((snap) => {
                if (snap.hasChild(id)) {
                    const statUpdater = {
                        updatedAction: 'Edited',
                        updatedBy: auth().currentUser.uid,
                        updatedDate: database.ServerValue.TIMESTAMP,
                    };
                    const setWebsiteUpdater = () => {
                        return new Promise((resolve, reject) => {
                            websitesRef
                                .child(id)
                                .once('value')
                                .then((websiteSnap) => {
                                    if (!websiteSnap.hasChild('editor')) {
                                        resolve(Object.assign({}, statUpdater, {
                                            editor: {
                                                idSite: uuidv4(),
                                                idPage: uuidv4(),
                                            },
                                        }));
                                    } else {
                                        resolve(Object.assign({}, statUpdater));
                                    }
                                });
                        });
                    };
                    const updateWebsite = (updater) => {
                        return new Promise((resolve, reject) => {
                            websitesRef
                                .child(id)
                                .update(updater)
                                .then(() => {
                                    resolve();
                                })
                                .catch(() => {
                                    reject();
                                });
                        });
                    };
                    const updateStat = (updater) => {
                        return new Promise((resolve, reject) => {
                            const websitesLogRef = ref.child(`websitesLog/${id}`);
                            websitesLogRef
                                .push(updater)
                                .then(() => {
                                    resolve();
                                })
                                .catch(() => {
                                    reject();
                                });
                        });
                    };
                    setWebsiteUpdater().then((websiteUpdater) => {
                        Promise
                            .all([
                                updateWebsite(websiteUpdater),
                                updateStat(statUpdater),
                            ])
                            .then(() => {
                                callback(websiteUpdater);
                            });
                    });
                } else {
                    callback('not exists');
                }
            });
    },
    publishPage(id, callback) {
        const websitesRef = ref.child(`websites`);
        websitesRef
            .once('value')
            .then((snap) => {
                if (snap.hasChild(id)) {
                    websitesRef
                        .child(id)
                        .update({
                            updatedAction: 'Published',
                            updatedBy: auth().currentUser.uid,
                            updatedDate: database.ServerValue.TIMESTAMP,
                        })
                        .then(() => {
                            const websitesLogRef = ref.child(`websitesLog/${id}`);
                            websitesLogRef
                                .push({
                                    updatedAction: 'Published',
                                    updatedBy: auth().currentUser.uid,
                                    updatedDate: database.ServerValue.TIMESTAMP,
                                })
                                .then(() => {
                                    callback('published');
                                });
                        });
                } else {
                    callback('not exists');
                }
            });
    },
    initializeClient(dashboardUrl, callback) {
        const contentWindow = createIframe(dashboardUrl);
        window.addEventListener('message', (event) => {
            const message = event.data;
            if (message.type === 'sglAccessToken') {
                const {
                    sglAccessToken,
                } = message.data;
                if (sglAccessToken) {
                    auth()
                        .signInWithCustomToken(sglAccessToken)
                        .then((data) => {
                            callback({
                                user: auth().currentUser,
                            });
                        })
                        .catch((error) => {
                            if (error.code === 'auth/invalid-custom-token' && auth().currentUser) {
                                callback({
                                    user: auth().currentUser,
                                });
                            } else {
                                callback({
                                    user: null,
                                });
                            }
                        });
                } else {
                    if (auth().currentUser) {
                        if (this.editedId) {
                            const websiteRef = ref.child(`websites/${this.editedId}`);
                            websiteRef
                                .update({
                                    onEdit: {
                                        status: false,
                                        user: auth().currentUser.uid,
                                    },
                                })
                                .then(() => {
                                    auth()
                                        .signOut()
                                        .then((data) => {
                                            callback({
                                                user: null,
                                            });
                                        })
                                        .catch((error) => {
                                            callback({
                                                user: null,
                                            });
                                        });
                                });;
                        } else {
                            auth()
                                .signOut()
                                .then((data) => {
                                    callback({
                                        user: null,
                                    });
                                })
                                .catch((error) => {
                                    callback({
                                        user: null,
                                    });
                                });
                        }
                    } else {
                        callback({
                            user: null,
                        });
                    }
                }
            }
        });
    },
};



